/* jshint browser:true, jquery:true */

@import '_var.scss';

/* оформление содержимого шаблона, которое находится под
header */
.block {
	padding: 60px 0;
}

.flex_cont {
	display: flex;
	border : 1px solid red;
}

.flex_cont_int {
	background-color: blue;
	border          : 3px solid orange;
	margin          : 10px;
	color           : #fff;
}
// .novotest_submit {
// 	line-height: 30px;
// }
#mindecte {
	padding: 0;
	margin : 0;
}




.title_block {
	@include font-size(30px);
	text-align    : center;
	margin-bottom : 10px;
	text-transform: uppercase;
}

.title_block_descr {
	@include font-size(16px);
	text-align    : left;
	margin-bottom : 20px;
	text-transform: uppercase;
	font-weight   : normal;
	line-height   : 1.53;
	border-bottom : 1px solid #eee;
}

.cont_title {
	color: red;
}

.padding_block {
	height          : 200px;
	background-color: red;
}

.block_h {
	height: 300px;
}

.grey_block {
	background-color: grey;
}

.blue_block {
	background-color: blue;
}

.Aqua_block {
	background-color: Aqua;

}

.BlueViolet_block {
	background-color: BlueViolet;

}

.Chocolate_block {
	background-color: Chocolate;
}

.test_block_cl {
	padding        : 0;
	margin         : 0;
	display        : flex;
	justify-content: flex-start;
	flex-wrap      : wrap;

}

.ol_list li {
	position    : relative;
	padding-left: 50px;
}

.ol_list li:before {
	position: absolute;
	top     : 0;
	content : '';
	left    : -1px;
}


.ol_list li {
	list-style: upper-roman outside;
}

.test_block_items {
	width       : 31%;
	margin-right: 2%;
}
#how_do {
	color: red;
}
#how_do_white {
	color: #fff;
}
.red {
	color: red;
}
.test_block_items:nth-child(3n+3) {
	margin-right: 0;
}

.switch_line {
	border-top: 1px solid red;
}

