// }

.b_w {
	padding-left : 10px;
	padding-right: 10px;
}


.submit_bb {
	text-transform  : uppercase;
	display         : block;
	margin          : 0 auto;
	text-align      : center;
	background-color: transparent;
	border          : 3px solid #fff;
	color           : #000;
	margin-bottom   : 50px;
	cursor          : pointer;
	font-size       : 1.4rem;
	padding         : 9px 33px;
	box-shadow      : 1px 1px 1px #95989A;
	margin-bottom   : 10px;
	@include trans;
}

.submit_bb:hover {
	box-shadow: none;
}



.ooo .custom-select__option,
.ooo2 .custom-select__option,
.ooo3 {
	pointer-events: none;
}

.ooo2 .custom-select__option.active,
.ooo .custom-select__option.active,
.ooo3.active {
	pointer-events: auto;
}

.calculator_idea_neactive button {
	cursor: pointer;
}


.calculator_idea_neactive {
	margin-bottom: 20px;
	margin-top   : 20px;
}

.calculator_idea_neactive button {
	padding         : 20px 30px;
	color           : #000;
	border          : 1px solid #000;
	text-transform  : uppercase;
	background-color: #fff;
}

.simpleel_list {
	margin-left: 20px;
}

.simpleel_list li {
	padding        : 5px 0;
	list-style-type: decimal !important;
}

.develo {
	display: none;
}

.visibl {
	visibility: visible;
}

.mouse_text_b button {
	padding  : 20px 10px;
	font-size: 20px;
}







